 .menu-bar{    
    display: flex;
    height: 50%;
}
.menu-bar__items{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;    
    background-color: #1F1E1E;
    padding: 15px;
    border-radius: 6px;

}

.menu-bar__item{
    color: #1F1E1E;
}

 .menu-bar__item >*{
  font-family: 'Inter', sans-serif;
    padding: 0.7rem;
    background-color: #1F1E1E;
    border: #844FFC 2px solid;
    border-radius: 6px;
} 

.menu-bar__item input::placeholder{
    color: white;
    font-size: 1rem;
}
.menu-bar__item select{
    color: white;
    font-size: 1rem;

}



 .menu-bar__item >*:focus{
    outline: none;
}

.menu-bar__item input:first-child {
    flex-grow: 1;
    margin-right: 40px;
    justify-self: flex-start;
}
  
  .menu-bar__item select:nth-child(2) {
    justify-self: center;
  }



.menu-bar__calculate button {
    background-color: #844FFC;
    color: white;
    padding: 1.4rem;
    width: 200px;
    font-size: 1rem;
    border: none;
    border-radius: 6px;    
    justify-self: flex-end;
    margin-left: 25px;

}
 
.menu-bar__calculate button:hover{
    background-color: #5E2EEA;
}

