@font-face {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    src: url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
  }
 
  html {
    height: 100%
}

body{
    font-family: 'Inter', sans-serif;
    background: linear-gradient(to right,#171717 50%, transparent 50%), url('./styles/kl.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.nomadcalc-wallpaper{
    background: linear-gradient(to right,#171717 50%, transparent 50%), url('./styles/kl.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.content-section{
    padding: 100px;
    color: white;
    display: flex;
    flex-direction: column ;
    align-items: flex-start;
}
.nomadcalc-item{
    margin-bottom: 30px;
    width: 50%;
}

.nomadcalc-item-twitter{
    width: 50%;
    color: #171717;
}